//font
@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
$font-f: 'Raleway', sans-serif;

//colors
$white: #fff;
$green: #0FBF3B;
$d-green: #6AA831;
$black: #101010;

//components
*, body{
    margin: 0;
    padding: 0;
    color: $green;
    font-family: $font-f;
}

body{
    width: 100%;
    height: 100vh;
    background: url('../img/bg.svg');
}

.main-container{
    padding: 30px 0 0;
    width: 80%;
    max-width: 1280px;
    margin: 0 auto;

    @media screen and (max-width:767px) {
        width: 85%;
    }
}

h1{
    font-size: 32px;
    font-weight: 900;
    color: $white;
    text-shadow: 0 0 15px $green;
}

header img{
    width: 100%;
    max-width: 135px;
}

.welcome-txt{
    text-align: center;

    h1{
        margin-bottom: 15px;
    }
}


.flex-container{
    margin-top: 30px;
    display: flex;
    text-align: center;
    
    .half{
        width: 50%;
        display: flex;
        flex-direction: column;
        align-items: center;

        h1{
            @media screen and (max-width:767px) {
                font-size: 20px;
            }
        }
    }

    .wide{
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 15px;

        h3{
            text-transform: uppercase;
            text-align: left;
            color: $white;
        }
        p{
            margin-top: 10px;
        }
    }
}

.choices{
    margin-top: 15px;
   
    h2{
        text-align: center;
        color: $white;
    }
}

.btn-container{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 25px;
}

.choice-btn{
    width: 30%;
    max-width: 70px;
    height: 70px;
    background-color: $white;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 5px solid $green;
    
    border-radius: 100%;

    transition: all .5s ease-in-out;
    cursor: pointer;
    margin-right: 50px;

    @media screen and (max-width: 374px) {
        margin-right: 25px;
    }

    &:hover, &:focus, &:active{
        transform: scale(1.1);
        box-shadow: 0 0 5px 1px $green;
    }
   
    &:last-child{
        margin-right: 0;
    }

    .choice-image{
        width: 60%;
    }

    &.choice-big{
        width: 90%;
        max-width: 150px;
        height: 150px;
        cursor:auto;
        margin-right: 0;
        box-shadow: 0 0 15px 10px $green;
        margin-top: 40px;

        @media screen and (max-width:767px) {
            max-width: 120px;
            height: 120px;
        }

        @media screen and (max-width: 374px) {
            max-width: 90px;
            height: 90px;
        }

        &:hover{
            transform: none;
        }
    }
}

.results{
    margin-top: 40px;
    width: 100%;
    text-align: center;

    h1{
        @media screen and (max-width:767px) {
            font-size: 24px;
        }
    }
    h2{
        margin-top: 10px;
        @media screen and (max-width:767px) {
            font-size: 18px;
        }
    }
}

.btn-main{
    position: relative;
    height: 70px;
    color: $white;
    font-size: 20px;
    font-weight: 500;
    text-transform: uppercase;
    padding: 10px 50px;
    outline: none;
    background: none;
    border: 5px solid $white;
    cursor: pointer;
    box-shadow: inset 0 0 5px 2px $d-green, 0 0 5px 2px $d-green;

    transition: all .5s linear;

    &:hover, &:active{
        color: $green;
        box-shadow: inset 0 0 10px $green, 0 0 9px 3px $green;
    }
}

footer{
    display: flex;
    justify-content: flex-end;
    padding: 25px 0;
}